@import "_variables";
@import "_mixins";
@import "header";
@import "_nav";
@import "_card";
@import "stars";

body,
h2,
p {
  margin: 0;
  font-family: Roboto, sans-serif;
  color: $colorOfText;
}

.main-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 50px auto;
  width: $cardWidth*$columnCount + ($columnCount - 1) * $gapX;
}
